<template>
	<section id="signin" class=" d-flex align-items-center">

    <div class="container">
      <div class="row">
        <div class="col-lg-7 d-flex flex-column justify-content-center">
          <div class="signin-lgo">
         <img src="../assets/images/workwire.png">
       </div>


        </div>

           <div class="col-lg-5 d-flex  justify-content-center align-items-center pt-4 ">

           <div class="signin-box_b">
            <div class="sub-text">
            <h4> Sign in</h4>
           <!--  <h5> Choose a display name</h5> -->
            <div class="join-workshire_b">
              
              <form v-on:submit.prevent="LoginUser" method="post" class="body">
                 <div class="form-group">
              
            <input type="text" name="email" class="form-control" placeholder="Email adresse" v-model="post.email" required>
             </div>
             <div class="form-group">
            <input type="password" name="password" class="form-control" placeholder="Password" v-model="post.password" required>
             <p v-if="passwordError">{{ passwordError }}</p>
             </div>
            

            <div class="box-join_b">
            <button  type="submit" class="join-signin_b">Countinue <img src="../assets/images/Group.png"></button>
            
         </div>
        </form>
         <div class="or-line">
          <p> OR</p>

         </div>
   
</div>
<div class="all-social">
  <div>
<div>
<a href="#" class="alradt-acount_b"> <img src="../assets/images/Facebook-Logo.png"> <label> Continue with Facebook </label>  </a>
</div>
<div>
<a href="#" class="alradt-acount_c"> <img src="../assets/images/Google-Logo.png"> <label> Continue with Facebook </label>  </a>
</div>
<div>
<a href="#" class="alradt-acount_d"> <img src="../assets/images/Apple-Logo.png"> <label> Continue with Facebook </label>  </a>
</div>

<div>
  <a href="#" class="alradt-acount-inn "> <label>New user ?   </label> <span>Create an account</span> <img src="../assets/images/back-arow.png"> </a>

</div>
</div>
</div>

<a href="#" class="alradt-inn"><span>Get help signing in </span>  </a>


</div>


           </div>


            </div>
          
           </div>


        <!-- <div class="col-lg-6 order-1 order-lg-2 hero-img" >
          <img src="image/sliderbg.png" class="img-fluid " alt="">
        </div> -->
      </div>
    </div>

  </section>


</template>
<script>
import {
    loginRequest
} from './ApiFunctions/index';

export default {
  name: "Signinstep2",
  data() {
        return {
            errorMessage: '',
            passwordError: '',
            post: {
                email:localStorage.getItem("email"),
                password: ''
            }
        }
    },
    mounted(){
      this.email = localStorage.getItem("email");
      console.log("email",this.email);
      if(this.$hostname){
          this.$router.push('/');
      }
    },
    methods: {
        async LoginUser() {
            this.email = localStorage.getItem("email");
            console.log("email",this.email);
            localStorage.setItem("loggedIn",true);
            
            const res = await loginRequest('/login', this.post)
            console.log("res",JSON.stringify(res));
            if(res.passwordError){
              this.passwordError = res.passwordError;
            }else{
              if (res.auth) {
                localStorage.setItem("firstName",res.data.firstName);
                localStorage.setItem("lastName",res.data.lastName);
                localStorage.setItem("email",res.data.email);
                this.$router.push({path:'/'});
              } else if (res.error) {
                  this.errorMessage = res.error;
              }
            }
            

        }
    }
}
</script>


